.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Input */

.App-input {
  display: inline-block;
  padding: 0.8em;
  outline: none;
  border: 1px solid #DDD;
  border-radius: 3px;
  font-size: 16px;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  width: 200px;
}

.App-input:focus {
  width: 300px;
}

.App-input-icon {
  margin-right: 0.8em;
}

/* Button */

.App-show-btn {
  display: inline-block;
  padding: 0.5em 1em;
  text-decoration: none;
  background: #668ad8;
  color: #FFF;
  border-bottom: solid 4px #627295;
  border-radius: 3px;
  margin: 0.8em;
}

.App-show-btn:active {
  -ms-transform: translateY(4px);
  -webkit-transform: translateY(4px);
  transform: translateY(4px);
  border-bottom: none;
}

/* Photos */

.App-photo img {
  padding:5px;
  opacity:0.8;
  filter:alpha(opacity=80);
}

.App-photo img:hover {
  opacity:1;
  filter:alpha(opacity=100);
}

/* Colors */

.App-attention {
  color: #e74c3c;
}

.App-success {
  color: #2ecc71;
}


